.universities {
  padding: 15px 0 11px;
  background-color: #f0f6f9;
  &-list__items {
    margin: 0;
    padding: 0 15px;
  }
  &__item {
    display: block;
    width: 133px;
    height: 30px;
    background: url("/wp-content/themes/ppc-template2/img/icons/universities.png") no-repeat;
    list-style: none;
    &.princeton {
      background-position: -205px 0;
    }
    &.stanford {
      background-position: -405px 0;
    }
    &.oxford {
      background-position: -604px 0;
    }
    &.alberta {
      background-position: -805px 0;
    }
    &.columbia {
      background-position: -1011px 0;
    }
  }
}

/*1200px*/
@media (max-width: @max-md){
  .universities {
    padding: 25px 0;
    &-list__items.df_jc-spBet {
      flex-direction: column;
      align-items: center;
    }
    &__item {
      height: 56px;
      &.london {
        background-position: 10px 15px;
      }
      &.princeton {
        background-position: -207px 15px;
      }
      &.stanford {
        background-position: -411px 15px;
      }
      &.oxford {
        background-position: -609px 15px;
      }
      &.alberta {
        background-position: -807px 15px;
      }
      &.columbia {
        background-position: -1002px 15px;
      }
    }
  }
}