.ceiu_writers-slider {
	background: #eef1f3;
	padding-bottom: 30px;
	@media (min-width: @min-xs) {
		padding-bottom: 70px
	}

	.section-title {
		padding-bottom: 60px;
	}

	&__container {
		position: relative;
	}
	&__items-wrap {
		position: relative;
		min-height: 250px;
		max-width: 370px;
		background: #fff;
		border:1px solid #e6eaef;
		border-radius: 5px;
		box-shadow: 1px 1px 15px 1px rgba(1,1,1,0.1);
		@media (min-width: @min-xs) {
			min-height: 250px;
		}
		.trig {
			position: absolute;
			width: 30px;
			height: 17px;
			background: none;
			bottom: -17px;
			left: 160px;
			box-shadow: 1px 1px 15px 1px rgba(158, 181, 205, 0.1);
			@media (min-width: @min-xs) {
				background: url("./img/trig.png") no-repeat;
			}

		}
	}
	&__items {
		padding: 40px 20px 0px 25px;
	}
	&-name {
		color: #000;
		font-family: "open_sanssemibold", sans-serif;
		font-size: 20px;
		float: left;
	}
	&-rating {
		color: @c4;
		font-size: 16px;
		float: right;
	}
	&-text {
		color: #727272;
		font-family: "open_sansregular", sans-serif;
		font-size: 15px;
		display: inline-block;
		line-height: 30px;
		margin-top: 15px;
		padding-bottom: 15px;
		@media (min-width: @min-xs) {
			padding-bottom: 0;
		}
	}
	&-ava {
		text-align: center;
		margin: 38px 0 15px;
		@media (min-width: @min-xs) {
			margin: 38px 0 0;
		}
	}
	.big-btn-wrap {
		padding-top: 50px;
	}
}