.services {
  padding: 80px 0 120px;
  background-color: #f7f8fa;
  font-family: 'Montserrat';

  &-title__wrapp {
    margin-bottom: 80px;
    text-align: center;
  }
  &-title {
	color: #1d2e40;
  }
  &-subtitle {
    color: #4b5a6b;
  }
  &-cards__wrapp {
    margin: 0;
    padding: 0 15px;

  }
  &-card {
    width: 270px;
    min-height: 420px;
    margin: 0 auto;
    background: #ffffff;
    list-style: none;
    &__img {
      display: block;
      margin-bottom: 24px;
    }
    &__descr-wrap {
      text-align: center;
      max-width: 220px;
      margin: 0 auto;
      font-weight: 400;
      line-height: 20px;
    }
    &__title {
      padding: 0 5px;
      color: #1d2e40;
      font-size: 16px;
      text-transform: uppercase;
    }
    &__descript {
      min-height: 60px;
      color: #4b5a6b;
      font-size: 14px;
      line-height: 20px;
    }
    &__stars {
      margin-bottom: 20px;
      color: #fdb846;
    }
    &__status {
      min-height: 39px;
      padding: 0 20px;
      color: #4b5a6b;
      font-size: 12px;
      font-weight: 400;
      line-height: 20px;
      border-top: 1px solid #ededed;
    }
    &__icon {
      display: inline-block;
      width: 12px;
      height: 12px;
      margin-right: 5px;
      background: #000000;
      &.helper {
        background: url("/wp-content/themes/ppc-template2/img/services/icon-clock.svg") no-repeat center;
      }
      &.online {
        background: url("/wp-content/themes/ppc-template2/img/services/icon-online.svg") no-repeat center;
      }
    }
  }
}
.card-f {
  -webkit-animation-duration: .6s;
  -o-animation-duration: .6s;
  -moz-animation-duration: .6s;
  animation-duration: .6s;

  -webkit-animation-delay: 0.4s;
  -o-animation-delay: 0.4s;
  -moz-animation-delay: 0.4s;
  animation-delay: 0.4s;
}
.card-tw {
  -webkit-animation-duration: .6s;
  -o-animation-duration: .6s;
  -moz-animation-duration: .6s;
  animation-duration: .6s;

  -webkit-animation-delay: 0.5s;
  -o-animation-delay: 0.5s;
  -moz-animation-delay: 0.5s;
  animation-delay: 0.5s;
}
.card-th {
  -webkit-animation-duration: .6s;
  -o-animation-duration: .6s;
  -moz-animation-duration: .6s;
  animation-duration: .6s;

  -webkit-animation-delay: 0.6s;
  -o-animation-delay: 0.6s;
  -moz-animation-delay: 0.6s;
  animation-delay: 0.6s;
}
.card-fv {
  -webkit-animation-duration: .6s;
  -o-animation-duration: .6s;
  -moz-animation-duration: .6s;
  animation-duration: .6s;

  -webkit-animation-delay: 0.7s;
  -o-animation-delay: 0.7s;
  -moz-animation-delay: 0.7s;
  animation-delay: 0.7s;
}