.ceiu_service-desc {
	&__wrap {
		position: relative;
		z-index: 1;
		margin-top: 30px;
	}
	&__block {
		margin: 30px 0;
		padding: 15px 30px;
		border: 2px solid @c8;
		color: @c8;
		border-radius: 14px;
	}
	&__title {
		margin-bottom: 30px;
		font-size: 24px;
		font-weight: 400;
		text-align: center;
		font-family: "open_sanssemibold", sans-serif;
		text-transform: uppercase;
	}
	&__list {
		li {
			margin-bottom: 30px;
			font-size: 15px;
			color: #353535;
			overflow: hidden;
		}
	}
	&__text {
		display: inline-block;
		max-width: 65%;
	}
	&__price {
		float: right;
		font-family: "open_sansbold", sans-serif;
	}
	&__basic {
		.custom-list li:before {color: #353535;}
		&-price-block {text-align: center;}
		&-text {
			display: inline-block;
			vertical-align: middle;
			margin-right: 10px;
			font-size: 16px;
			font-weight: 400;
			color: @c8;
			font-family: "open_sansregular", sans-serif;
			&-price {
				font-size: 24px;
				color: @c8;
				font-family: "open_sansbold", sans-serif;
			}
		}
		@media (max-width: 535px) {
			img {
				max-width: 90%;
			}
		}
	}
	&__additional {
		.custom-list li:before {color: @c8;}
		&-info {
			font-size: 16px;
			text-align: justify;
			text-align-last: center;
			&:before {
				content: '';
				display: block;
				width: 25%;
				margin: 30px auto 0;
				padding: 0 0 15px;
				border-top: 1px solid @c8;
			}
		}
	}
	.big-btn-wrap {
		text-align: center;
		margin: 20px 0 80px;
	}
	@media (min-width: @min-xs) {
		&__basic {
			&-text {font-size: 20px;}
		}
		&__additional {
			&-info {
				font-size: 18px;
				&:before {
					margin-top: 60px;
					padding-bottom: 30px;
				}
			}
		}
	}
	@media (min-width: @min-md) {
		.equal-cols {
			display: flex;
			> div {display: flex;}
		}
		&__block {width: 100%;}
	}
}
