/*ORDER-STEPS
-------------------------------------------------*/
.ceiu_order-steps {
  padding-bottom: 30px;
}
.ceiu_order-steps__item {
  max-width: 300px;
  padding: 30px 0;
  margin: 0 auto 80px;
  text-align: center;
  position: relative;
}
.ceiu_order-steps__item:before,
.ceiu_order-steps__item:after {
  content: '';
  position: absolute;
  bottom: 100%;
  right: 50%;
  height: 52px;
  margin-bottom: 12px;
  transform: rotate(90deg);
}
.ceiu_order-steps__item:before {
  width: 52px;
  margin-right: -26px;
  background: url(img/order-steps-arrow.png);
}
.ceiu_order-steps__item:after {
  width: 29px;
  margin-right: -15px;
  background: url(img/order-steps-arrow-animated.png);
  animation-name: orderStepsArrowBottom;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: steps(4, end);
}
.ceiu_order-steps__col-1 .ceiu_order-steps__item:before,
.ceiu_order-steps__col-1 .ceiu_order-steps__item:after {
  content: none;
}
.ceiu_order-steps__icon {
  width: 90px;
  height: 90px;
  line-height: 90px;
  margin: 0 auto 10px;
  font-size: 50px;
  color: #fdb846;
  text-align: center;
}
.ceiu_order-steps__text {
  font-size: 15px;
  color: #727272;
  font-family: "open_sansregular", sans-serif;
}
.ceiu_order-steps__col-4 .ceiu_order-steps__item {
  margin-bottom: 0;
}
.ceiu_order-steps__col-4 .ceiu_order-steps__icon {
  border: 2px solid #fdb846;
  border-radius: 100%;
}
.ceiu_order-steps__col-4 .ceiu_order-steps__text {
  color: #fdb846;
}
@media (min-width: 768px) {
  .ceiu_order-steps__item {
    margin: auto;
    padding: 60px 0;
  }
  .ceiu_order-steps__item:before,
  .ceiu_order-steps__item:after {
    top: 100px;
    right: 100%;
    margin: 0;
    transform: none;
  }
  .ceiu_order-steps__item:after {
    animation-name: orderStepsArrowRight;
  }
  .ceiu_order-steps__icon {
    width: 150px;
    height: 150px;
    line-height: 150px;
    margin-bottom: 30px;
    font-size: 72px;
  }
}
@keyframes orderStepsArrowBottom {
  from {
    margin-bottom: 23px;
  }
  to {
    margin-bottom: -8px;
  }
}
@keyframes orderStepsArrowRight {
  from {
    margin-right: 23px;
  }
  to {
    margin-right: -8px;
  }
}
