.ceiu_services-tabs ul {
  margin: 0;
  padding: 0;
}
.ceiu_services-tabs li {
  list-style-type: none;
  margin-bottom: 15px;
  font-size: 13px;
}
.ceiu_services-tabs__tablist {
  overflow: hidden;
  margin-bottom: -2px;
}
.ceiu_services-tabs__tablist-item {
  float: left;
  margin-right: 10px;
  padding: 12px 20px;
  border: 2px solid #f1f1f1;
  font-size: 18px;
  text-transform: uppercase;
  cursor: pointer;
  font-family: "open_sansregular", sans-serif;
  background: #fff;
}
.ceiu_services-tabs__tablist-item:last-child {
  margin-right: 0;
}
.ceiu_services-tabs__tablist-item.active-tab {
  border-bottom-color: #fff;
  cursor: default;
  color: #fdb846;
}
.ceiu_services-tabs__content-item {
  border: 2px solid #f1f1f1;
  padding: 40px 30px 30px;
  display: none;
  background: #fff;
}
.ceiu_services-tabs__content-item.active-tab {
  display: block;
}
.ceiu_services-tabs__list-title {
  margin-bottom: 15px;
  font-weight: 700;
  font-size: 13px;
}
.ceiu_services-tabs__wrap {
  padding-top: 15px;
}
.ceiu_services-tabs__col:first-child .ceiu_services-tabs__wrap:first-child {
  padding-top: 0;
}
@media (min-width: 468px) {
  .ceiu_services-tabs__tablist-item {
    padding: 12px 20px;
  }
  .ceiu_services-tabs__content-subjects {
    padding: 40px 30px 30px;
  }
  .ceiu_services-tabs__content-subjects ul {
    font-size: 0;
    margin: 0 -15px;
  }
  .ceiu_services-tabs__content-subjects li {
    display: inline-block;
    vertical-align: top;
    width: 50%;
    padding: 0 15px;
  }
  .ceiu_services-tabs__wrap {
    border-top: 1px solid #eee;
  }
  .ceiu_services-tabs__col:first-child .ceiu_services-tabs__wrap:first-child {
    border: 0;
  }
}
@media (min-width: 768px) {
  .ceiu_services-tabs__tablist-item {
    padding: 12px 40px;
  }
}
@media (max-width: 467px) {
  .ceiu_services-tabs__tablist-item {
    padding: 12px 9px;
    font-size: 13px;
  }
}
@media (min-width: 992px) {
  .ceiu_services-tabs__content-subjects li {
    display: block;
    width: auto;
  }
  .ceiu_services-tabs__wrap {
    border: 0;
    padding-top: 0;
    margin-bottom: 30px;
  }
}
.ceiu_services-tabs__content-what-we-write {
  font-size: 13px;
  color: #929292;
  line-height: 1.5;
}
.ceiu_services-tabs__content-what-we-write__item {
  margin-bottom: 30px;
}
.ceiu_services-tabs__content-what-we-write__title {
  font-weight: 700;
  margin-bottom: 15px;
}
/*PAGE: FRONT-PAGE
-------------------------------------------------*/
/*PAGE: BLOG
-------------------------------------------------*/
/*BLOG: ARTICLES*/
.site_tpl-blog .site_article {
  overflow: hidden;
  padding-bottom: 15px;
  border-bottom: 1px dashed #eee;
}
.site_tpl-blog .site_article-title {
  margin-bottom: 15px;
}
/*BLOG: PAGINATION*/
.site_blog-pagination {
  margin: 30px 0;
  text-align: center;
}
.site_blog-pagination .nav-links {
  font-size: 0;
}
.site_blog-pagination .page-numbers {
  display: inline-block;
  text-decoration: none;
  padding: 2px 7px;
  font-size: 18px;
}
.site_blog-pagination .page-numbers:hover,
.site_blog-pagination .page-numbers:active,
.site_blog-pagination .page-numbers:focus,
.site_blog-pagination .page-numbers.current {
  color: #222;
}
.site_blog-pagination .screen-reader-text {
  display: none;
}
/*PAGE: 404
-------------------------------------------------*/
.site_tpl-404 {
  padding: 30px 0;
  text-align: center;
}
.site_tpl-404 .site_article-title {
  margin: 0;
  font-size: 200px;
  font-weight: 400;
  line-height: 1;
  text-align: center;
}
.site_tpl-404 .site_article-text {
  font-size: 20px;
}
/*PAGE 404: RESPONSIVE*/
@media only screen and (max-width: 767px) {
  .site_tpl-404 .site_article-title {
    font-size: 100px;
  }
  .site_tpl-404 .site_article-text {
    font-size: 16px;
  }
}
/*CUSTOM SCROLLBAR
-------------------------------------------------*/
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background: #0078b1;
}
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
  -webkit-box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.5);
  background: #fe9e8f;
}
