.security {
  position: relative;
  padding: 80px 0 120px;
  color: #ffffff;
  background-color: #f7f8fa;
  font-family: 'Montserrat';
  background: url("/wp-content/themes/ppc-template2/img/security/security-bg.png") no-repeat center;

  &-lock__wrapp {
    position: absolute;
    top: 55%;
    left: 50%;
    width: 357px;
    height: 357px;
    transform: translate(-50%, -50%);
  }
  &-title__wrapp {

  }
  &-title {
    color: #ffffff;
  }
  &-subtitle {
    margin-bottom: 65px;
    color: #ffffff;
  }
  &-features__wrapp {
    margin: 0;
    padding: 0 15px;
    li {
      margin-bottom: 65px;
    }
  }
  &-features__icon {
    display: block;
    margin: 0 auto;
  }
  &-featur {
    text-align: center;
    &__descr-wrap {
      max-width: 270px;
    }
    &__title {
      padding: 0 5px;
      font-size: 18px;
      font-weight: 500;
    }
    &__descript {
      padding: 0 5px;
      color: #d8def8;
      font-family:"Helvetica Neue", Helvetica, Arial, sans-serif;
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
    }
  }
  &-btn__wrap {
    max-width: 430px;
    margin: 0 auto;
  }
}


/*992px*/
@media (max-width: @min-md){
  .security {
    padding: 80px 0 60px;
    background-size: cover;

    .component-title__wrapp {
      margin-bottom: 385px;
    }
  }

  .security-btn__wrap {
    &.df_jc-spBet {
      flex-direction: column;
      align-items: center;
    }
    .btn-call {
      max-width: 88%;
      width: 100%;
      margin: 0 auto 45px;
    }
  }
  .security-lock__wrapp {
    position: absolute;
    top: 235px;
    left: 50%;
    width: 100%;
    max-width: 357px;
    height: 357px;
    transform: translate(-50%, 0);
    text-align: center;
  }
  .security-features__wrapp {
    li {
      flex-direction: column;
      margin-bottom: 0;
    }

  }
  .security-featur {
    margin-bottom: 45px;
    &__descr-wrap {
      max-width: 670px;
      margin: 0 auto;
    }
  }
}
