.benefits {

  padding: 55px 0 60px;
  background-color: #fff;
  &-list__items {
    margin: 0;
    padding: 0 15px;
  }
  &__item {
    width: 348px;
    height: 78px;
    list-style: none;
    font-family: 'Montserrat';
    &.princeton {
      background-position: -205px 0;
    }
    &.stanford {
      background-position: -405px 0;
    }
    &.oxford {
      background-position: -604px 0;
    }
    &.alberta {
      background-position: -805px 0;
    }
    &.columbia {
      background-position: -1011px 0;
    }
  }
  &__icon {
    width: 76px;
    height: 76px;
    &-wrapp {
      min-width: 76px;
      height: 76px;
      margin-right: 17px;
    }
  }
  &__title {
    margin: 0;
    color: #2c79ff;
    font-size: 18px;
    font-weight: 500;
    line-height: 28px;
  }
  &__descript {
    margin: 0;
    color: #233769;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
  }
}


/*992px*/
@media (max-width: @max-md){
  .benefits {
    padding: 40px 0 10px;
    &-list__items.df_jc-spBet {
      flex-direction: column;
      align-items: center;
    }
    &__item {
      width: 100%;
      max-width: 348px;
      height: auto;
      margin-bottom: 30px;
      text-align: center;
      &.df {
        flex-direction: column;
      }
    }
  }
}

@media (max-width: @max-sm) {
  .benefits__icon-wrapp {
    margin: 0;
    margin-bottom: 20px;
  }
}