/* ----------------------------------------------------------------------
   RESET
   ---------------------------------------------------------------------- */

:focus {
  outline: none;
}

html {
  height: 100%;
}

body {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  height: 100vh;
  min-height: 100vh;
  min-width: 300px;
  max-width:100%;
  width:100%;
  background-color: #fff;
  font-family: @montserrat;
  font-size: 16px;
  color: @gc1;
  @media (max-width: @max-lg){
    font-size: 14px;
  }
}

ul{
  padding: 0;
}

a {
  outline: none;
  color: @gc1;
  text-decoration: none;

  &:hover {
    color: darken(@gc1,10%);
  }
}

img {
  max-width: 100%;
  height: auto;
}

/* ----------------------------------------------------------------------
   LAYOUT
   ---------------------------------------------------------------------- */

#feedbacks-widget .customer-reviews-title{
  display: none;
}

.price-table{
  background: #fff;
  &-content{
    border-bottom:3px solid #e6e7eb !important;
  }
}

body .article ul li {
  background: transparent;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
  color: @gc1;
  line-height: 40px;
  &:before{
    content: '\f00c';
    font-family: 'FontAwesome';
    color: @gc2;
    margin-right: 10px;
  }
}

article h1,
article h2,
article h3{
  color: @gc1;
  font-weight: 600;
  text-align: center;
  line-height: 50px;
  margin-bottom: 50px;
}

.article__content{
  p{
    color: @gc1;
    line-height: 25px;
  }
}

.menu{
  padding-left: 0;
  margin-bottom: 0;
  li{
    display: inline-block;
    list-style: none;
  }
}

.btn-wrap{
  text-align: center;
}

.component{
  &-main{
    background-image: url('images/buy-essay/main-bg.jpg');
    background-repeat: no-repeat;
    background-position: 53% -120px;
  }
  &-main-2{
    //
  }
  &-wrap{
    max-width: 1170px;
    margin: auto;
    @media(max-width: @max-lg){
      padding: 0 15px;
    }
  }
  &-title__wrapp {
     margin-bottom: 80px;
     text-align: center;
   }
  &-title{
    max-width: 610px;
    margin: 0 auto 20px;
    text-align: center;
    font-size: 36px;
    font-weight: 400;
    line-height: 45px;
    letter-spacing: -1px;
    @media (max-width: @max-lg){
      br{
        display: none;
      }
    }
    @media (max-width: @max-md){
      font-size: 32px;
    }
  }
  &-subtitle {
    margin: 0;
    font-size: 18px;
    font-weight: 400;
    span{
      color: @rc;
      &.span-gc2{
        color: @gc2 !important;

      }
    }
    @media (max-width: @max-lg){
      br{
        display: none;
      }
    }
    @media (max-width: @max-md){
      font-size: 16px;
    }
  }
}

//login btn
#of-widgets-login-tooltip .of-widgets-open-tooltip{
  background-color: #fff !important;
}

//slick
.slicknav_menu{
  display: none;
}

input{
  color: #fff;
}