.process {
  position: relative;
  padding: 80px 0 120px;
  /*color: #ffffff;*/
  background-color: #fff;
  font-family: 'Montserrat';

  &-title__wrapp {

  }
  &-title {
    color: #cccccc;
  }
  &-subtitle {
    margin-bottom: 75px;
    color: #4b5a6b;
  }
  &-features__wrapp {
    margin: 0;
    padding: 0;
  }

  &-featur {
    position: relative;
    max-width: 270px;
    margin: 0 auto;
    padding: 0 20px 0 0;
    list-style: none;
    &:after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      width: 12px;
      height: 12px;
      background: url("/wp-content/themes/ppc-template2/img/icons/arrow.svg") no-repeat center;
    }

    &__number{
      margin-bottom: 10px;
      color: #2c79ff;
      font-size: 30px;
      font-weight: 500;
      line-height: 45px;
      letter-spacing: -0.83px;
    }

    &__descr-wrap {
    }
    &__title {
      margin: 0 0 10px;
      color: #1d2e40;
      font-size: 15px;
      font-weight: 600;
      line-height: 20px;
      text-transform: uppercase;
      letter-spacing: 1px;
    }
    &__descript {
      margin: 0;
      color: #4b5a6b;
      font-size: 14px;
      font-weight: 400;
      line-height: 25px;
      letter-spacing: -0.3px;
    }
  }
  .owl-item:last-child {
    .process-featur {
      &:after {
        opacity: 0;
      }
    }
  }
}
