.ceiu_writers-selection {
	background: #f7f7f7;
	overflow: hidden;
	.section-title {
		padding-bottom: 0;
		.subtitle {
			font-size: 30px;
		}
	}
	.vertical-base {
		height: calc(100%);
		position: absolute;
		left: -2px;
		width: 3px;
		background: linear-gradient(to top, rgba(0,0,0,0), rgba(234, 109, 109, 1), rgba(0,0,0,0));
	}
	&__content {
		max-width: 460px;
		margin: 0 auto 30px;
		color: #2d2d2e;
		padding-left: 30px;
		height: 400px;
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: center;
		border-style: solid;
    border-image: linear-gradient(to top, rgba(255,0,0,0), rgba(255,0,0,1), rgba(255,0,0,0)) 1 100%;
    border-width: 0 0 0 3px;
		.circle-list {
			position: absolute;
			left: -15px;
		}
	}
	&__title {
		margin: 30px 0 10px;
		font-size: 20px;
		font-weight: 400;
		text-transform: uppercase;
	}
	&__text {
		font-size: 16px;
		text-align: justify;
		padding: 10px 0;
		&::before {
			content: "";
			display: block;
			position: absolute;
			left: 6px;
			width: 20px;
			height: 20px;
			border: 5px solid #ec8080;
			border-radius: 10px;
			background-color: #fff;
			margin-top: 10px;
		}
		@media(max-width: 767px) {
			position: relative;
			&::before {
				left: -50px;
			}
		}
		@media (max-width: 485px) {
			&::before {
				left: -42px;
			}
		}
	}
	@media (min-width: @min-sm) {
		.valign {
			display: flex;
			align-items: center;
		}
		&__content {
			margin: 0;
			max-height: 488px;
			overflow: hidden;
		}
	}
	@media (min-width: @min-md) {
		&__content {max-height: 400px;}
		&__text {font-size: 17px;}
	}
}

/*TESTIMONIALS: SLIDER*/
.selection-slider {
	margin: auto;
	padding: 0 20px 0 24px;
	list-style: none;
	width: 283px;
	height: 488px;
	background-size: contain;
	position: relative;
	display: flex !important;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	@media (max-width: 991px) {
		padding: 0;
		width: 327px;
	}
	@media (max-width: 767px) {
		height: 250px;
	}
	@media (max-width: 400px) {
		width: 100%;
	}
	.owl-controls {
		margin: -30px 0 0 0;
		position: relative;
		@media (max-width: 479px) {
			margin: 0;
		}
		.owl-page {
			span {
				background: transparent;
				border: 2px solid #959595;
				margin: 10px 7px;
			}
			&.active span, &:hover span {
				border-color: #000;
				background: #000;
			}
		}
	}
	.rating, .author {
		text-align: right;
	}
	.rating {
		color: #4e87cc;
	}
	@media (min-width: @min-xs) {
		.owl-controls {
			margin: 0 0 0 20px;
			.owl-page {
				&.active span, &:hover span {
					border-color: #000;
					background: #000;
				}
			}
		}
	}
	@media (min-width: @min-md) {
		margin: 0;
		width: 406px;
		height: 400px;
			.owl-controls {margin: 0 0 0 27px;}
	}
}
