.pay-meth {
  background-color: #f0f6f9;

  &-features__wrapp {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0;
    border-right: 1px solid #d7e0eb;
    border-left: 1px solid #d7e0eb;
  }

  &__featur {
    position: relative;
    width: 400px;
    height: 110px;
    list-style: none;

    &:nth-child(2) {
      border-right: 1px solid #d7e0eb;
      border-left: 1px solid #d7e0eb;
    }
  }
  &__icon {
    width: 136px;
    height: 48px;
    background: url("/wp-content/themes/ppc-template2/img/pay-meth/pay-methods.png") no-repeat;
    &.paypal {
      background-position: 0 0;
    }
    &.mcafee {
      background-position: -388px 0;
    }
    &.secure {
      background-position: -796px 0;
    }
  }
}


@media (max-width: @max-md){
  .pay-meth {
    &-features__wrapp {
      &.df_jc-c {
        flex-direction: column;
        align-items: center;
      }
    }
    &__featur {
      width: 100%;
    }
  }
  .pay-meth__featur:nth-child(2) {
    border-right: none;
    border-left: none;
    border-top: 1px solid #d7e0eb;
    border-bottom: 1px solid #d7e0eb;
  }
}