/*TOP-BLOCK (for inner pages)
-------------------------------------------------*/
.top-block {
	min-height: 380px;
	padding: 120px 0 30px;
	background: url(img/top-slider/1.jpg) no-repeat 50% 0;
	background-size: cover;
	display: flex;
	align-items: center;
	justify-content: center;
	color: red;
	&__title {
		margin-bottom: 15px;
		font-size: 30px;
		color: #fff;
		text-align: center;
	}
	&__text {
		font-size: 14px;
		color: #fff;
		text-align: center;
	}
	@media (min-width: @min-md) {
		&__title {font-size: 50px;}
	}
}

/*PAGE: PRICES
-------------------------------------------------*/
/*PRICES-INFO*/
.prices-info {
	&:extend(.services-info all);
}

/*PAGE: GUARANTEES
-------------------------------------------------*/
@import "include/inc-guarantees";
.top-block {
	.services-info {
		padding: 0;
		&__title, &__text {color: #fff;}
		@media (min-width: @min-sm) {
			&__item {padding: 0}
		}
	}
}

/*PAGE: WRITERS
-------------------------------------------------*/
.writers-info-1 {
	.text-wrap {
		padding: 40px 0 60px;
		ul {
			max-width: 520px;
			font-size: 18px;
			color: #727272;
		}
		p {
			font-size: 14px;
			line-height: 1.66;
			text-align: justify;
			text-align-last: center;
			color: #929292;
		}
	}
}
.writers-info-2 {
	.text-wrap {
		padding: 40px 0 20px;
		p {
			margin-bottom: 40px;
			font-size: 18px;
			line-height: 1.66;
			text-align: justify;
			color: #929292
		}
	}
	.img-wrap {text-align: center;}
	@media (min-width: @min-md) {
		.valign {
			display: flex;
			align-items: center;
		}
	}
}

/*PAGE: SERVICES
-------------------------------------------------*/
/*GUARANTEES-INFO*/
.guarantees-info {
	&:extend(.services-info all);
	padding: 0 0 60px 0;
	.section-title {margin-bottom: 60px;}
}

