/*CUSTOM SCROLLBAR
-------------------------------------------------*/
::-webkit-scrollbar {
	width: 6px;
	height: 6px;
	background: @c1;
}
::-webkit-scrollbar-thumb {
	-webkit-border-radius: 10px;
	-webkit-box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.5);
	background: @c2;
}
