/*HEADER: LAYOUT*/
.ceiu_header {
	display: none;
	flex-shrink: 0;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 10;
	padding: 25px 0 0;
	border-color: #fff;
	transition: 0.25s;
	&.animated {
		padding: 8px 0;
		border-color: #000;
		background-color: #222;
		background-color: rgba(238, 243, 249, 0.8);
	}
	.valign {
		display: flex;
		align-items: center;
	}
	&.mmenu-open {background: rgba(238, 243, 249, 0.8);}
}
.ceiu_header-other {
	background: rgba(0,0,0,0.8);
}

/*HEADER: LOGO*/
.ceiu_header__logo {
	a {
		color: #eee;
		&:hover, &:active, &:focus {color: @c1;};
		display: block;
	}
	& embed {
		pointer-events: none;
		width: 80%;
		@media (max-width: 400px) {
			width: 100%;
		}
	}
}

/* HEADER: ORDER BUTTON */
.order-btn {
	padding: 5px 15px;
	border: 2px solid #fe9e8f;
	font-size: 12px;
	text-transform: uppercase;
	border-radius: 30px;
	color: @c5;
	text-decoration: none;
	&:hover, &:active, &:focus {
		text-decoration: none;
		background: @c2;
		border-color: @c2;
		background: darken(@c2, 5%) !important;
		border-color: darken(@c2, 5%) !important;
		color: #000;
	}
	&-wrap {text-align: right;}
}

/*HEADER: NAV*/
.nav {
	ul {
		display: flex;
		justify-content: flex-end;
		align-items: center;
	}
	a {
		padding: 5px;
		font-size: 12px;
	}
}
.nav, .mobile-nav {
	ul {
		margin: 0;
		padding: 0;
	}
	li {
		list-style-type: none;
		display: inline-block;
		margin: 0 25px;
	}
	a {
		display: inline-block;
		text-transform: uppercase;
		color: #ffffff;
		/*font-family: Montserrat;*/
		font-size: 11px;
		font-weight: 700;
		text-transform: uppercase;
	}
	a:hover, a:active, a:focus,
	.current-menu-item a,
	.current_page_item a {
		color: #00adff;
		border-bottom: 1px solid #fff;
	}
}
.mobile-nav {
	display: none;
	&-visible {
		display: inline-block;
		vertical-align: middle;
	}
}

/*HEADER: SLICKNAV MENU*/
.slicknav {
	&_menu {
		display: inline-block;
		vertical-align: middle;
		overflow: hidden;
		padding-right: 15px;
	}
	&_open .slicknav_icon-bar{background:@c1;}
	&_btn {
		float: right;
		font: 700 16px/20px Arial, sans-serif;
		z-index: 10000000;
	}
	&_menutxt, &_icon {
		float: left;
	}
	&_menutxt {
		display: none;
	}
	&_icon-bar {
		display: block;
		font: 700 16px/20px Arial, sans-serif;
		width: 1.5em;
		height: 0.125em;
		margin: 3px;
		background: @c3;
	}
	&_nav {
		position: absolute;
		left: 0;
		top: 100%;
		z-index: 10;
		width: 100%;
		padding: 30px 0;
		clear: both;
		background: lighten(@c3, 10%);
		background: rgba(238, 243, 249, 0.8);
		border-bottom: 2px solid #000;
		text-transform: uppercase;
		text-align: center;
		overflow: auto;
		ul {margin: 0}
		li {display: block;}
		a {
			font-size: 16px;
			padding: 10px;
		}
	}
}

@media (max-width: @max-lg) {
	.mobile-nav {display: block;}
	/*.nav {display: none;}*/
}
@media (max-width: 400px) {
	.button-wrapper, .logo-wrapper {
		padding: 0 5px;
	}
}

