.header {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 2;
	width: 100%;
	/*background: #000000;*/

	&-wrap{
		/*padding: 10px 0 50px;*/
		position: relative;
		@media (max-width: @max-lg){
			padding: 10px 15px 50px;
			flex-direction: row;
		}
		@media (max-width: @max-md){
			padding: 10px 0 10px !important;
		}
	}
	&-item{
		flex: 1 0 20%;
		@media (max-width: @max-md){
			padding: 0 5px;
			&:last-child{
				flex: 1 0 30%;
			}
		}
	}
	.nav-menu-item{
		flex: 1 0 40%;
		@media (max-width:@max-md){
			flex: 1 0 5%;
			order: 1;
			max-width: 50px;
		}
	}
	.phone-item{
		text-align: center;
	}
	.btn-item{
		text-align: right;
	}
	.btn-login,
	.header-order-btn{
		display: inline-block;
	}
	.btn-login{
		#of-widgets-login-tooltip .of-widgets-open-tooltip{
			padding: 7px 15px 6px;
			animation: 2s infinite ease-in-out;
			display: inline-block;
			&:before{
				content: '\f090';
				display: inline-block;
				font-family: 'FontAwesome';
				color: @gc2;
				padding-right: 5px;
				@media(max-width: @max-md){
					padding-right: 0;
				}
			}
			&:hover{
				box-shadow: 0 4px 8px rgba(45, 42, 38, 0.2);
			}
			span{
				@media(max-width: @max-md){
					display: none;
				}
			}
		}
		#of-widgets-login-tooltip .of-widgets-login-popup{
			@media (max-width: @max-md){
				right: -50px;
			}
		}
	}
	.header-order-btn{
		margin-left: 15px;
		@media (max-width:@max-sm){
			margin-left: 5px;
		}
	}
	.header-order{
		width: 116px;
		height: 44px;
		display: inline-block;
		padding: 15px 0;
		text-align: center;
		/* font-family: Montserrat; */
		font-size: 11px;
		font-weight: 700;
		text-transform: uppercase;
		color: #ffffff;
		box-shadow: 0 2px 10px #1467b1;
		background-color: #f09209;
		border-radius: 5px;
		border: 1px solid transparent;
		transition: all 0.2s ease;
		border: 1px solid transparent;
		transition: all 0.2s ease;
		&:before{
			content: '\f290';
			display: inline-block;
			display: none;
			font-family: 'FontAwesome';
			font-weight: 400;
			color: #fff;
			@media (min-width: @min-md){
				padding-right: 5px;
			}

		}
		&:hover{
			text-decoration: none;
			/*background-color: darken(@rc, 10%);*/
			transform: scale(1.05);
			border-color: #f09209;
			background: #fff;
			color: #f09209;
		}
		span{
		}
	}
}


/*1200px*/
@media (max-width: @max-lg){
	.header {
		.phone-item{
			display: none;
		}
	}
}

.nav{
	/*font-family: @montserrat;*/
	font-size: 11px;
	font-weight: 700;
	color: @gc1;
	text-transform: uppercase;
	text-align: center;
}

#menu-navigation,
#menu-navigation{
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	margin: 0;
}

.header-logo__img {
	width: 147px;
	height: 27px;
}


@media screen and (max-width: @max-md) {
	.header-logo__img {
		width: 120px;
		height: 21px;
		margin-left: 25px;
	}
	.hamburger {
		display: inline-block;
		z-index: 2;
		text-align: center;
		cursor: pointer;
		margin-left: 5%;
	}
	.hamburger:after {
		font-family: 'fontAwesome';
		content: '\f0c9';
		font-size: 20px;
		color: #f7f7f7;
	}

	.close-menu {
		//background-color: #efefef;
	}
	.close-menu:after {
		font-family: 'fontAwesome';
		content: '\f00d';
	}
	#menu-top-menu,
	#menu-navigation {
		position: absolute;
		left: -999px;
	}

	.hamburger.close-menu + #menu-top-menu,
	.hamburger.close-menu + #menu-navigation {
		flex-flow: column nowrap;
		background-color: #efefef;
		width: 100%;
		position: absolute;
		left: 0;
		top: 100%;
		padding: 30px 0px;
		margin: 0;
		z-index: 10;
		background: url(/wp-content/themes/ppc-template2/img/main-screen/m-s-bg-md.png) no-repeat 100% center;
		background-size: 100%;
	}

	.move {
		left: 0;
	}

	#menu-top-menu a,
	#menu-navigation a {
		display: block;
		text-align: center;
		border-bottom: 1px solid #989898;
		padding: 20px 0;
	}
	#menu-top-menu a:hover,
	#menu-navigation a:hover {
		color: #000;
	}
	.menu.move {
		.menu-item {
			width: 90%;
			margin: 0 auto;
		}
	}
}

.logo-item {
	padding: 35px 0;
}

@media (max-width: @max-xs) {
	.main-screen {
		background-size: 100% 100%;
	}
}