.ceiu_services-info {
	padding: 30px 0;
	&__item {
		max-width: 320px;
		margin: auto;
		padding: 30px 0;
	}
	&__icon {
		font-size: 30px;
		color: @c3;
	}
	&__title {
		color: #000;
		font-size: 16px;
		text-transform: uppercase;
	}
	&__text {
		font-size: 14px;
		line-height: 20px;
		color: #929292;
		text-align: justify;
		padding-top: 10px;
	}
}
