.ceiu_services-tabs {
	padding-bottom: 80px;
	background: #f7f8f8;
	.section-title {margin-bottom: 80px;}
	ul {
		margin: 0;
		padding: 0;
	}
	li {
		list-style-type: none;
		margin-bottom: 15px;
		font-size: 13px;
	}
	&__tablist {
		overflow: hidden;
		margin-bottom: -2px;
	}
	&__tablist-item {
		float: left;
		margin-right: 10px;
		padding: 12px 20px;
		border: 2px solid #f1f1f1;
		font-size: 18px;
		text-transform: uppercase;
		cursor: pointer;
		font-family: "open_sansregular", sans-serif;
		background: #fff;
		&:last-child {
			margin-right: 0;
		}
		&.active-tab {
			border-bottom-color: #fff;
			cursor: default;
			color: #fdb846;
		}
	}
	&__content-item {
		border: 2px solid #f1f1f1;
		padding: 40px 30px 30px;
		display: none;
		background: #fff;
		&.active-tab {display: block;}
	}
	&__list-title {
		margin-bottom: 15px;
		font-weight: 700;
		font-size: 13px;
	}
	&__wrap {padding-top: 15px;}
	&__col:first-child &__wrap:first-child {padding-top: 0;}
	@media (min-width: @min-xs) {
		&__tablist-item {padding: 12px 20px;}
		&__content-subjects {
			padding: 40px 30px 30px;
			ul {
				font-size: 0;
				margin: 0 -15px;
			}
			li {
				display: inline-block;
				vertical-align: top;
				width: 50%;
				padding: 0 15px;
			}
		}
		&__wrap {border-top: 1px solid #eee;}
		&__col:first-child &__wrap:first-child {border: 0;}
	}
	@media (min-width: @min-sm) {
		&__tablist-item {
			padding: 12px 40px;
		}
	}
	@media (max-width: @max-xs) {
		&__tablist-item {
			padding: 12px 9px;
			font-size: 13px;
		}
	}
	@media (min-width: @min-md) {
		&__content-subjects {
			li {
				display: block;
				width: auto;
			}
		}
		&__wrap {
			border: 0;
			padding-top: 0;
			margin-bottom: 30px;
		}
	}
	&__content-what-we-write {
		font-size: 13px;
		color: #929292;
		line-height: 1.5;

		&__item {
			margin-bottom: 30px;
		}
		&__title {
			font-weight: 700;
			margin-bottom: 15px;
		}
	}
}
