html, body {font: 300 16px/1.25 "open_sansregular", sans-serif;}
body {
	margin: 0;
	background: #fff;
	color: #929292;
	display: flex;
	flex-direction: column;
	height: 100vh;
	min-height: 100vh;
	min-width: 300px;
}

.page .examples {
	background-color: #000;
}

.main {
	flex: 1 0 auto;
	&::before {
		display: block;
		content: '';
		height: 40px;
	}
}
.main-front {
	flex: 1 0 auto;
}
.main-other {padding-top: 68px;}
img {
	max-width: 100%;
	height: auto;
}

/*COMMON: ALIGNMENTS*/
.alignleft {
	float: left;
	margin: 0.25em 1em 1em 0;
	clear: both;
}
.alignright {
	float: right;
	margin: 0.25em 0 1em 1em;
	clear: both;
}
.aligncenter {
	display: block;
	margin: 0.25em auto 1em auto;
	clear: both;
}
canvas {
    display: block;
    margin: 15px 0;
}


.component{
	&-main{
		background-image: url('images/buy-essay/main-bg.jpg');
		background-repeat: no-repeat;
		background-position: 53% -120px;
	}
	&-main-2{
		//
	}
	&-wrap{
		max-width: 1170px;
		margin: auto;
		@media(max-width: @max-lg){
			padding: 0 15px;
		}
	}
	&-title{
		text-align: center;
		font-size: 48px;
		line-height: 40px;
		/*font-family: @montserrat_alternates;*/
		font-weight: 700;
		color: @gc1;
		letter-spacing: -1.2px;
		span{
			color: @gc2;
		}
		@media (max-width: @max-lg){
			br{
				display: none;
			}
		}
		@media (max-width: @max-md){
			font-size: 32px;
		}
	}
	&-subtitle {
		text-align: center;
		font-size: 22px;
		/*font-family: @montserrat_alternates;*/
		font-weight: 500;
		color: @gc1;
		letter-spacing: -0.2px;
		span{
			color: @rc;
			&.span-gc2{
				color: @gc2 !important;

			}
		}
		@media (max-width: @max-lg){
			br{
				display: none;
			}
		}
		@media (max-width: @max-md){
			font-size: 16px;
		}
	}
}