.main-screen {
  padding: 158px 0 136px 0;
  background: url("/wp-content/themes/ppc-template2/img/main-screen/m-s-bg.png") no-repeat top center;

  &__content-wrap {
    max-width: 528px;
    color: #ffffff;
    font-family: 'Montserrat';
  }

  &__title {
    margin: 0 0 30px 0;
    font-size: 58px;
    font-weight: 600;
    line-height: 70px;
    text-transform: uppercase;
    & span {
      letter-spacing: -1.07px;
    }
  }

  &__sub-title {
    margin: 0 0 70px 0;
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    & span {
      letter-spacing: -0.2px;
    }
  }

}
.screen__content-wrap {
  max-width: 528px;
}
.icon {
  display: inline-block;
  width: 27px;
  height: 26px;
  margin-right: 8px;
  &.quote {
    background: url("/wp-content/themes/ppc-template2/img/icons/quote.svg") no-repeat center;
  }
  &.messenger {
    background: url("/wp-content/themes/ppc-template2/img/icons/messenger.svg") no-repeat center;
  }
}

/*768px*/
@media (max-width: @max-sm){
  .main-screen {
    padding: 158px 0 0 0;
    background: url("/wp-content/themes/ppc-template2/img/main-screen/m-s-bg-md.png") no-repeat bottom center;
    background-size: 100%;
    &__content-wrap {
      margin: 0 auto;
      padding-bottom: 365px;
      text-align: center;
      background: url("/wp-content/themes/ppc-template2/img/main-screen/boy.png") no-repeat bottom center;
    }
    &__btn-contein {
      flex-direction: column;
      align-items: center;
      .btn-call {
        max-width: 88%;
        width: 100%;
        margin: 0 auto 35px;
      }
    }
  }
}

/*460px*/
@media (max-width: @max-xs) {
  .main-screen {
    background-size: 100% 100%;
  }
  .main-screen__title {
    font-size: 42px;
  }
}