.ceiu_choose {
	padding-bottom: 30px;
	@media (min-width: @min-xs) {
		padding-bottom: 70px
	}
	.section-title {
		padding-bottom: 40px;
	}
	.valign {
		display: flex;
		align-items: center;
	}
	&__item {
		margin: 15px auto 25px;
		max-width: 260px;
		white-space: nowrap;
		.i-1 {
			background-image: url("img/icons/i-1.png");
		}
		.i-2 {
			background-image: url("img/icons/i-2.png");
		}
		.i-3 {
			background-image: url("img/icons/i-3.png");
		}
		.i-4 {
			background-image: url("img/icons/i-4.png");
		}
		.i-5 {
			background-image: url("img/icons/i-5.png");
		}
		.i-6 {
			background-image: url("img/icons/i-6.png");
		}
	}
	&__icon {
		display: inline-block;
		vertical-align: middle;
		width: 70px;
		height: 70px;
		margin-right: -50px;
		position: relative;
		background-repeat: no-repeat;
		background-position: 50% 50%;
		background-size: contain;
	}
	.col-r {
		display: inline-block;
		vertical-align: middle;
		margin-left: 50px;
		padding-left: 10px;
		white-space: normal;
	}
	&__title {
		color: @c7;
		font-size: 16px;
		font-family: "open_sanssemibold", sans-serif;
		text-transform: uppercase;
		margin-bottom: 5px;
	}
	&__text {
		color: #727272;
	}

	@media (min-width: @min-xs) {
		&__item {
			max-width: 340px;
		}
	}
	@media (min-width: @min-md) {
		&__item {
			margin: 35px auto 0;
			max-width: 450px;
		}

		&__icon {
			margin-right: -70px;
		}

		&__title {
			font-size: 16px;
		}

		.col-r {
			margin-left: 70px;
		}
	}
}