.file-item {
  position: relative;
  padding-right: 10px;

  div[data-role='remove'] {
    width: 8px;
    height: 8px;
    cursor: pointer;
    display: block;
    position: absolute;
    top: 7px;
    right: 0;

    &:after,
    &:before {
      content: '';
      border: 1px solid #c72e00;
      width: 11px;
      position: absolute;
    }

    &:before {
      transform: rotate(45deg);
    }
    &:after {
      transform: rotate(-45deg);
    }
  }
}