.ceiu_counters {
	background-color: #e2e2e2;
	&__item {
		max-width: 360px;
		margin: 45px auto;
		text-align: center;
	}
	&__num {
		font-size: 52px;
		font-weight: 400;
		color: #ea6969;
	}
	&__text {
		font-size: 20px;
		color: #5a5a5a;
		text-transform: uppercase;
	}
	&__col+&__col {
		border-left: 1px solid #fff;
	}
}
