.ceiu_footer {
	flex-shrink: 0;
	&__top {
		padding: 30px 0 15px;
		background: #011e2c;
		text-align: center;
		&-social-links {
			text-align: center;
			margin-bottom: 15px;

			@media (min-width: 768px){
				margin-bottom: 5px;
			}
			a {
				display: inline-block;
				margin: 4px 2px;
				width: 36px;
				height: 36px;
				line-height: 36px;
				border-radius: 100%;
				font-size: 15px;
				text-align: center;
				background: #2c3d44;
				color: #011e2c;
				transition: .25s;
				&:hover {
					text-decoration: none;
					background: #fff;
				}
			}
		}
		&-terms {
			ul {
				margin: 0;
				padding: 0;
				text-align: center;
				li {
					list-style-type: none;
					display: inline-block;
					&::after {
						content: '';
						display: inline-block;
						vertical-align: middle;
						height: 12px;
						margin: 0 2px;
					}
					&:last-child::after {
						content: none;
					}
					a {
						margin: 3px 0;
						font-size: 12px;
						color: #fff;
						&:hover {
							color: #fff;
							text-decoration: underline;
						}
					}
				}
			}
		}
	}
	&__logo {
		margin-bottom: 30px;
		a {
			display: block;
		}
		embed {
			pointer-events: none;
		}
	}
	&__payments {
		margin-bottom: 30px;
		clear: both;
		@media(min-width: 320px) {
			margin: 12px 0;
		}
	}
}
.form-bottom {
	max-width:300px;
}

.form-bottom .section-title {
	text-align: center;
	position: relative;
	margin-bottom: 40px;
	font-weight: 500;
	font-size: 30px;
	font-family: Roboto, sans-serif;
	color: #171b2c
}

.form-bottom .form-outer input {
	display: block;
	width: 100%;
	background: #f6f6f6;
	border: none;
	-webkit-border-radius: 4px;
	-moz-border-radius: 4px;
	border-radius: 4px;
	margin: 3px 10px 20px;
	padding: 15px 20px;
	color: #747c83;
	resize: none;
	vertical-align: middle
}

.form-bottom .form-outer input:focus {
	background: rgba(246, 246, 246, .4)
}

.form-bottom .form-outer input[type=text] {
	margin-left: 0
}

.form-bottom .form-outer input[type=email] {
	margin-right: 0;
	margin-left: 0;
}


.form-bottom .form-outer [type=submit] {
	display: block;
	width: 100%;
	padding: 15px 0;
	text-transform: uppercase;
	font-weight: 700;
	color: #fff;
	background: #50d17a;
	-webkit-border-radius: 4px;
	-moz-border-radius: 4px;
	border-radius: 4px;
	border: none;
	margin-right: 10px;
	position: relative
}

.form-bottom .form-outer button:not(.disabled):before {
	content: '';
	position: absolute;
	right: 20px;
	top: 50%;
	margin-top: -10px;
	width: 20px;
	height: 20px;
	border: 4px solid;
	border-left-color: transparent;
	-webkit-border-radius: 50%;
	-moz-border-radius: 50%;
	border-radius: 50%;
	opacity: 0;
	-webkit-transition-duration: .5s;
	-moz-transition-duration: .5s;
	-o-transition-duration: .5s;
	transition-duration: .5s;
	-webkit-transition-property: opacity;
	-o-transition-property: opacity;
	-moz-transition-property: opacity;
	transition-property: opacity;
	-webkit-animation-duration: 1s;
	-moz-animation-duration: 1s;
	-o-animation-duration: 1s;
	animation-duration: 1s;
	-webkit-animation-iteration-count: infinite;
	-moz-animation-iteration-count: infinite;
	-o-animation-iteration-count: infinite;
	animation-iteration-count: infinite;
	-webkit-animation-name: rotate;
	-moz-animation-name: rotate;
	-o-animation-name: rotate;
	animation-name: rotate;
	-webkit-animation-timing-function: linear;
	-moz-animation-timing-function: linear;
	-o-animation-timing-function: linear;
	animation-timing-function: linear
}

.form-bottom .form-outer button:not(.disabled).sending {
	pointer-events: none;
	cursor: not-allowed
}

.form-bottom .form-outer button:not(.disabled).sending:before {
	-webkit-transition-delay: .5s;
	-moz-transition-delay: .5s;
	-o-transition-delay: .5s;
	transition-delay: .5s;
	-webkit-transition-duration: 1s;
	-moz-transition-duration: 1s;
	-o-transition-duration: 1s;
	transition-duration: 1s;
	opacity: 1
}

.form-bottom .form-outer button:not(.disabled).sent {
	-webkit-animation-name: bounceIn;
	-moz-animation-name: bounceIn;
	-o-animation-name: bounceIn;
	animation-name: bounceIn;
	-webkit-animation-duration: .75s;
	-moz-animation-duration: .75s;
	-o-animation-duration: .75s;
	animation-duration: .75s
}

.modal_div {
	font-family: 'Open Sans', sans-serif, Arial, sans-serif;
	line-height: 18px;
	position: fixed;
	top: 45%;
	left: 50%;
	margin-top: -300px;
	margin-left: -250px;
	display: none;
	opacity: 0;
	padding: 20px 10px;
	width: 320px;
	height: auto;
	z-index: 99999;
	right: 0;
	background: #fff;
	-webkit-box-shadow: 0 0 15px 2px #323232;
	-moz-box-shadow: 0 0 15px 2px #323232;
	box-shadow: 0 0 15px 2px #323232;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	border-radius: 3px;
	-webkit-box-sizing: content-box;
	-moz-box-sizing: content-box;
	box-sizing: content-box
}

.modal_div .modal_close {
	cursor: pointer;
	display: block;
	width: 11px;
	height: 11px;
	position: absolute;
	top: 20px;
	right: 20px
}

.modal_div .modal_close:before {
	content: '';
	border: 1px solid #636a76;
	width: 11px;
	-webkit-transform: rotate(45deg);
	-moz-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	-o-transform: rotate(45deg);
	transform: rotate(45deg);
	margin:4px 0 0;
	position: absolute
}

.modal_div .modal_close:after {
	content: '';
	border: 1px solid #636a76;
	width: 11px;
	-webkit-transform: rotate(-45deg);
	-moz-transform: rotate(-45deg);
	-ms-transform: rotate(-45deg);
	-o-transform: rotate(-45deg);
	transform: rotate(-45deg);
	margin:4px 0 0;
	position: absolute
}

.modal_div .modal-body {
	margin: 25px 10px;
	padding:0;
}

#overlay {
	z-index: 99998;
	position: fixed;
	background-color: #000;
	opacity: .7;
	-moz-opacity: .8;
	filter: alpha(opacity=80);
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	cursor: pointer;
	display: none
}

.footer-constructor {
	padding: 40px 0;
	background: #1d2e40;
	.df {
		justify-content: space-between;
		align-items: center;
	}
}
.footer-featur__wrapp {
	width: 100%;
	max-width: 390px;
}
.footer__logo {
	&-big{
		width: 176px;
		height: 60px;
	}
	&-small {
		display: none;
	}
}
.footer__service-badge {
	img {
		width: 230px;
		height: 96px;
	}
}
.footer__desclaimer {
	display: flex;
	justify-content: flex-end;
	&-big {
		width: 281px;
		height: 32px;
	}
	&-small {
		display: none;
	}
}


@media (max-width: @max-sm){

	.footer-featur__wrapp {
		margin-bottom: 40px;
	}
	.footer-constructor {
		padding: 40px 0 0;
	}
	.footer-constructor .df {
		flex-direction: column;
	}
	.footer__logo {
		&-big{
			display: none;
		}
		&-small {
			display: block;
			width: 147px;
			height: 27px;
			margin: 0 auto;
		}
	}
	.footer__desclaimer {
		justify-content: center;
		&-big{
			display: none;
		}
		&-small {
			display: block;
			margin: 0 auto;
		}
	}
}