//login btn
#of-widgets-login-tooltip .of-widgets-open-tooltip{
  color: @gc2 !important;
  font-family: @montserrat !important;
  font-size: 11px !important;
  text-transform: uppercase;
}

.btn-wrap{
  text-align: center;
}


//btn order red
.red-btn{
  background-color: @rc;
  background-image: linear-gradient(to top, rgba(18, 16, 11, 0.2) 0%, rgba(0, 0, 0, 0) 100%);
  font-family: @montserrat_alternates;
  color: #fff;
  text-align: center;
  font-weight: 500;
  font-size: 18px;
  min-width: 230px;
  padding: 20px 0;
  display: inline-block;
  &:hover{
    background-color: darken(@rc, 10%);
    text-decoration: none;
    color: #fff;
  }
}

//btn order red
.white-btn{
  color: @rc;
  border-radius: 10px;
  background: #fff;
  background-image: linear-gradient(to top, #f5f5f5 0%, #fafafa 100%);
  border-bottom: 2px solid #dbdbdb;
  font-family: @montserrat_alternates;
  font-weight: 500;
  font-size: 18px;
  min-width: 230px;
  padding: 15px;
  display: inline-block;
  box-shadow: 0 8px 16px rgba(45, 42, 38, 0.2);
  box-shadow: 0 3px 0 #dbdbdb;
  &:hover{
    background: darken(#fff, 5%);
    color: darken(@rc, 10%);
    text-decoration: none;
  }
  @media (max-width: @max-xs){
    font-size: 15px;
  }
}

.btn-tmp {
  border-radius: 4px;
  text-align: center;
  transition: all 0.4s ease;
}
.btn-call {
  /*display: inline-block;*/
  width: 200px;
  height: 48px;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  user-select: none;
  background-color: #ffffff;
  border: 2px solid transparent;
  font-family: 'Montserrat';
  font-size: 12px;
  font-weight: 600;
  line-height: 20px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: all 0.4s ease;
  box-shadow: 0 1px 10px 2px rgba(26, 84, 193, 0.63);

  &.quote {
    color: #f09209;
    margin-right: 30px;
  }
  &.messenger {
    color: #0084ff;
  }

  &:hover {
    background-color: transparent;
    border-color: #fff;
    color: #fff;

    &.quote {
      color: #ffffff;
    }

    &.messenger {
      color: #ffffff;
    }
  }
}