.ceiu_features {
  background: @c7;
  padding: 10px 0;
  .valign {
    display: flex;
    align-items: center;
  }
  &__item {
    margin: 15px auto 25px;
    max-width: 260px;
    white-space: nowrap;
    .i-medal {
      background-image: url("img/icons/i-medal.png");
    }
    .i-note2 {
      background-image: url("img/icons/i-note2.png");
    }
    .i-piggy {
      background-image: url("img/icons/i-piggy.png");
    }
  }
  &__icon {
    display: inline-block;
    vertical-align: middle;
    width: 50px;
    height: 50px;
    margin-right: -50px;
    position: relative;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
  }
  .col-r {
    display: inline-block;
    vertical-align: middle;
    margin-left: 50px;
    padding-left: 10px;
    white-space: normal;
  }
  &__title {
    color: @c4;
    font-size: 16px;
    font-family: "open_sanssemibold", sans-serif;
    text-transform: uppercase;
    margin-bottom: 5px;
  }
  &__text {
    color: #fff;
  }

  @media (min-width: @min-xs) {
    &__item {
      max-width: 340px;
    }
  }
  @media (min-width: @min-md) {
    &__item {
      margin: 35px auto;
      max-width: none;
    }

    &__icon {
      margin-right: -70px;
      width: 70px;
      height: 70px;
    }

    &__title {
      font-size: 16px;
    }

    .col-r {
      margin-left: 70px;
    }
  }
}