.df {
  display: flex;

  &_center {
    /*df_center*/
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &-ai-c {
    /*df-ai-c*/
    align-items: center;
  }
  &_ai-c {
    /*df_ai-c*/
    display: flex;
    align-items: center;
  }
  &-ai-s {
    align-items: stretch;
  }
  &-ai-flEnd {
    align-items: flex-end;
  }
  &-ai-flStar {
    align-items: flex-start;
  }
  &-ac-c {
    align-content: center;
  }
  &-ac-s {
    align-content: stretch;
  }
  &-ac-flEnd {
    align-content: flex-end;
  }
  &-ac-flStar {
    align-content: flex-start;
  }
  &-ac-spA {
    /*df-ac-spA*/
    align-content: space-around;
  }
  &_ac-spA {
    /*df_ac-spA*/
    display: flex;
    align-content: space-around;
  }
  &-ac-spB {
    /*df-ac-spB*/
    align-content: space-between;
  }
  &_ac-spB {
    /*df_ac-spB*/
    display: flex;
    align-content: space-between;
  }
  &-jc-c {
    /*df-jc-c*/
    justify-content: center;
  }
  &_jc-c {
    /*df_jc-c*/
    display: flex;
    justify-content: center;
  }
  &-jc-spBet {
    /*df-jc-spBet*/
    justify-content: space-between;
  }
  &_jc-spBet {
    /*df_jc-spBet*/
    display: flex;
    justify-content: space-between;
  }
  &-jc-spAr {
    /*df-jc-spAr*/
    justify-content: space-around;
  }
  &_jc-spAr {
    /*df_jc-spAr*/
    display: flex;
    justify-content: space-around;

  }
  &-jc-flStar {
    justify-content: flex-start;
  }
  &-jc-flEnd {
    /*df-jc-flEnd*/
    justify-content: flex-end;
  }
  &-wrap {
    /*df-wrap */
    display: flex;
    flex-wrap: wrap;
  }
  /*df_fd-c, df_fd-cr, df_fd-r, df_fd-rr*/
  &_fd {
    &-c {
      display: flex;
      flex-direction: column;
      &r {
        display: flex;
        flex-direction: column-reverse;
      }
    }
    &-r {
      display: flex;
      flex-direction: row;
      &r {
        display: flex;
        flex-direction: row-reverse;
      }
    }
  }
  &-ac-flStar {
    align-content: flex-start;
  }
  @media (max-width: 768px) {
    &-wrap768 {
      flex-wrap: wrap;
    }
    &_wrap768 {
      display: flex;
      flex-wrap: wrap;
    }
  }
}