.ceiu_prices-info {
  &__item {
    text-align: center;
    color: #000;
    i {
      color: @c3;
    }
  }
  &__icon {
    font-size: 30px;
    color: #e83e3e;
  }
  &__title {
    color: #000;
    font-size: 16px;
    text-transform: uppercase;
  }
  &__text {
    text-align: center;
    font-size: 14px;
    line-height: 20px;
    color: #929292;
    padding-top: 10px;
  }
}
